import { get, post, config } from '@/utils/api.js';
export default {
	name: 'activityRule',
	props: {
		// 是否展示
		wxOpenSettingIsShow: {
			type: Boolean,
			default: false
		},
		// 是否 开启动画
		isStartAnimation: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			staticUrl: config.staticUrl,
			actAni: '', // 动画所需配置
			actStyle: {
				color: 'red'
			}
		};
	},
	methods: {
		// 关闭弹窗
		closeWxOpenSetting() {
			const that = this;
			that.$emit('WxOpenSettingColse', false);
		},
		// 关闭弹窗
		openSetting() {
			const that = this;
			that.$emit('openSetting', false);
		},

		// 开始动画
		startAnimation() {
			console.log(3);
			const that = this;
			// 活动规则 从下向上滑出
			const actAnimation = wx.createAnimation({
				duration: 500,
				timingFunction: 'ease',
				delay: 500
			});
			actAnimation.translateY(0).step();

			that.actAni = actAnimation.export();
		},

		isStartAnimationFun(e) {
			console.log('e');
			console.log(e);
			const that = this;
			this.startAnimation();
			if (this.isStartAnimation) {
				this.startAnimation();
			}
		}
	}
};
